<template>
  <v-container grid-list-xl fluid>
    <header-title title="Reportes"></header-title>
    <v-row class="mb-6 text-center">
      <v-col cols="12" sm="6" md="4" class="py-1">
        <v-select
          label="Convocatorias"
          v-model="convocatoria_id"
          :items="param.convocatorias"
          item-text="cite"
          item-value="id"
          hide-details
          filled
          @change="changeAnnouncement"
        ></v-select>
      </v-col>
      <v-col cols="12" sm="6" md="4" class="py-1">
        <v-autocomplete
          label="Instituciones"
          v-model="institucion_id"
          :items="param.instituciones"
          item-text="nombre"
          item-value="id"
          hide-details
          filled
          @change="getEstadisticas()"
        ></v-autocomplete>
      </v-col>
    </v-row>
    <div v-if="dato.modalidades.length > 0">
      <v-row class="mb-6 text-center">
        <v-col
          cols="12"
          v-for="(item, index) in dato.modalidades"
          :key="index"
          sm="4"
          md="2"
        >
          <v-card :color="colorQuads[index]" dark class="mx-auto" outlined>
            <v-list-item three-line>
              <v-list-item-content>
                <div class="subtitle-1 mb-2">
                  {{ item.descripcion.toUpperCase() }}
                </div>
                <v-list-item-title class="text-h5 mb-1">
                  {{ item.total }}
                </v-list-item-title>
                <v-list-item-subtitle>
                  Postulantes inscritos
                </v-list-item-subtitle>
              </v-list-item-content>
            </v-list-item>
          </v-card>
        </v-col>
        <v-col cols="12" sm="4" md="2">
          <v-card color="#263238" dark class="mx-auto" outlined>
            <v-list-item three-line>
              <v-list-item-content>
                <div class="subtitle-1 mb-2">TOTAL</div>
                <v-list-item-title class="text-h5 mb-1">
                  {{ total }}
                </v-list-item-title>
                <v-list-item-subtitle>
                  Postulantes inscritos
                </v-list-item-subtitle>
              </v-list-item-content>
            </v-list-item>
          </v-card>
        </v-col>
      </v-row>
      <v-row>
        <v-col cols="12" sm="6" md="4">
          <v-card class="mx-auto" elevation="3">
            <highcharts class="chart" :options="chartModalidad"></highcharts>
          </v-card>
        </v-col>
        <v-col cols="12" sm="6" md="4">
          <v-card class="mx-auto" elevation="3">
            <highcharts class="chart" :options="chartGenero"></highcharts>
          </v-card>
        </v-col>
      </v-row>
      <v-row>
        <v-col cols="12" sm="6">
          <v-simple-table dense>
            <template v-slot:default>
              <thead>
                <tr class="head-color-1">
                  <th
                    class="text-center text-sm-subtitle-2 text-md-subtitle-1 font-weight-medium"
                    colspan="3"
                  >
                    CANTIDAD DE POSTULANTES INSCRITOS POR ESPECIALIDAD
                  </th>
                </tr>
                <tr class="head-color-2">
                  <th class="text-left">No.</th>
                  <th class="text-left">ESPECIALIDAD</th>
                  <th class="text-left">TOTAL</th>
                </tr>
              </thead>
              <tbody>
                <tr v-for="(item, index) in dato.especialidades" :key="index">
                  <td>{{ index + 1 }}</td>
                  <td>{{ item.descripcion }}</td>
                  <td>{{ item.total }}</td>
                </tr>
              </tbody>
            </template>
          </v-simple-table>
        </v-col>
        <v-col cols="12" sm="6">
          <v-simple-table dense>
            <template v-slot:default>
              <thead>
                <tr class="head-color-1">
                  <th
                    class="text-center text-sm-subtitle-2 text-md-subtitle-1 font-weight-medium"
                    colspan="3"
                  >
                    CANTIDAD DE HABILITADOS A PRUEBA DE APTITUD POR ESFM/UA
                  </th>
                </tr>
                <tr class="head-color-2">
                  <th class="text-left">No.</th>
                  <th class="text-left">ESPECIALIDAD</th>
                  <th class="text-left">TOTAL</th>
                </tr>
              </thead>
              <tbody>
                <tr v-for="(item, index) in dato.aptitudes" :key="index">
                  <td>{{ index + 1 }}</td>
                  <td>{{ item.descripcion }}</td>
                  <td>{{ item.total }}</td>
                </tr>
              </tbody>
            </template>
          </v-simple-table>
        </v-col>
      </v-row>
    </div>
    <div v-else>
      <span
        class="red--text"
        v-if="convocatoria_id === '' || institucion_id === ''"
      >
        Seleccione Convocatoria e Institución
      </span>
      <span v-else class="red--text"> No hay registros para visualizar. </span>
    </div>
  </v-container>
</template>

<script>
import Highcharts from "highcharts";
import highchartsMore from "highcharts/highcharts-more";
import exportingInit from "highcharts/modules/exporting";
import HeaderTitle from "@/components/HeaderTitle";
import Service from "../utils/general";
import { mapActions } from "vuex";
import axios from "axios";
exportingInit(Highcharts);
highchartsMore(Highcharts);
export default {
  name: "admision-reporte-institucion",
  components: {
    HeaderTitle,
  },
  data: () => ({
    message: "",
    loading: false,
    param: {
      convocatorias: [],
      instituciones: [],
    },
    convocatoria_id: "",
    institucion_id: "",
    dato: {
      modalidades: [],
      departamentos: [],
      especialidades: [],
      aptitudes: [],
    },
    chartModalidad: {},
    chartGenero: {},
    colorQuads: ["#689F38", "#EF6C00", "#00ACC1", "#BA68C8", "#039BE5"],
    user: null,
  }),
  computed: {
    total() {
      return this.dato.modalidades.reduce(
        (a, b) => a + (parseInt(b.total) || 0),
        0
      );
    },
  },
  created() {
    this.user = Service.getUser();
    if (this.user == null) {
      this.$router.replace({ name: "root" });
    }
  },
  mounted() {
    const roles = [1, 2,13];
    if (this.user && roles.includes(this.user.rid)) {
      this.getConvocatorias();
    } else {
      this.$router.replace({ name: "root" });
    }
  },
  methods: {
    ...mapActions(["toast"]),
    getConvocatorias() {
      axios
        .get(
          Service.getUrlAPI() + "admision/convocatoria/institucion",
          Service.getHeader()
        )
        .then((response) => {
          this.param = response.data;
          this.convocatoria_id =
            this.param.convocatorias[this.param.convocatorias.length - 1].id;
        })
        .catch(() => {
          console.error("Parametros no encontrado");
        });
    },
    changeAnnouncement() {
      this.dato.modalidades = [];
      this.institucion_id = "";
    },
    getEstadisticas() {
      if (this.convocatoria_id === "") {
        this.toast({ color: "info", text: "Selección la convocatoria" });
      } else {
        axios
          .post(
            Service.getUrlAPI() + "admision/dashboard/institucion",
            {
              convocatoria_id: this.convocatoria_id,
              institucion_id: this.institucion_id,
            },
            Service.getHeader()
          )
          .then((response) => {
            const colors = [
              "#9CCC65",
              "#F57C00",
              "#26C6DA",
              "#BA68C8",
              "#00B0FF",
            ];
            this.dato = response.data;
            let modalidad = {
              nombre: [],
              total: [],
            };
            const modalidades = response.data.modalidades;
            for (const key in modalidades) {
              modalidad.nombre.push(modalidades[key].descripcion);
              modalidad.total.push(modalidades[key].total * 1);
            }
            let genero = {
              nombre: [],
              total: [],
            };
            const generos = response.data.generos;
            for (const key in generos) {
              genero.nombre.push(generos[key].descripcion);
              genero.total.push(generos[key].total * 1);
            }
            this.chartModalidad = {
              chart: {
                type: "column",
              },
              title: {
                text: "POSTULANTES POR MODALIDAD",
              },
              subtitle: {
                text: "Total de postulantes inscritos por modalidad",
              },
              xAxis: {
                categories: modalidad.nombre,
                title: {
                  text: null,
                },
              },
              yAxis: {
                min: 0,
                title: {
                  text: "Total inscritos",
                },
                labels: {
                  overflow: "justify",
                },
              },
              tooltip: {
                enabled: true,
              },
              plotOptions: {
                column: {
                  colorByPoint: true,
                  dataLabels: {
                    enabled: true,
                    format: "{point.y}",
                  },
                },
              },
              legend: {
                enabled: false,
                layout: "vertical",
                align: "right",
                verticalAlign: "top",
                x: -40,
                y: 80,
                floating: true,
                borderWidth: 0,
                backgroundColor: "#FFFFFF",
                shadow: true,
              },
              credits: {
                enabled: false,
              },
              colors: colors,
              series: [
                {
                  name: "Total",
                  data: modalidad.total,
                },
              ],
              exporting: {
                buttons: {
                  contextButton: {
                    menuItems: [
                      {
                        text: "Imprimir",
                        textKey: "printChart",
                        onclick: function () {
                          this.print();
                        },
                      },
                      {
                        text: "Descargar",
                        textKey: "downloadJPEG",
                        onclick: function () {
                          this.exportChart({
                            type: "image/jpeg",
                          });
                        },
                      },
                    ],
                  },
                },
              },
            };
            this.chartGenero = {
              chart: {
                type: "column",
              },
              title: {
                text: "POSTULANTES POR GENERO",
              },
              subtitle: {
                text: "Total de postulantes inscritos por genero",
              },
              xAxis: {
                categories: genero.nombre,
                title: {
                  text: null,
                },
              },
              yAxis: {
                min: 0,
                title: {
                  text: "Total inscritos",
                },
                labels: {
                  overflow: "justify",
                },
              },
              tooltip: {
                enabled: true,
              },
              plotOptions: {
                column: {
                  colorByPoint: true,
                  dataLabels: {
                    enabled: true,
                    format: "{point.y}",
                  },
                },
              },
              legend: {
                enabled: false,
                layout: "vertical",
                align: "right",
                verticalAlign: "top",
                x: -40,
                y: 80,
                floating: true,
                borderWidth: 0,
                backgroundColor: "#FFFFFF",
                shadow: true,
              },
              credits: {
                enabled: false,
              },
              colors: ["#0091EA", "#00B8D4", "#00BFA5"],
              series: [
                {
                  name: "Total",
                  data: genero.total,
                  //color: "#4DD0E1",
                },
              ],
              exporting: {
                buttons: {
                  contextButton: {
                    menuItems: [
                      {
                        text: "Imprimir",
                        textKey: "printChart",
                        onclick: function () {
                          this.print();
                        },
                      },
                      {
                        text: "Descargar",
                        textKey: "downloadJPEG",
                        onclick: function () {
                          this.exportChart({
                            type: "image/jpeg",
                          });
                        },
                      },
                    ],
                  },
                },
              },
            };
          })
          .catch(() => {
            console.error("datos no encontrado");
          });
      }
    },
  },
};
</script>
<style scoped>
.head-color-1 {
  background-color: #616161;
}
.head-color-1 th {
  color: white !important;
}
.head-color-2 {
  background-color: #e0e0e0;
}
</style>